.EditData {
  position: absolute;
  z-index: 3;
  top: -.5em;
  left: 0;
  width: 100%;
  margin-bottom: 3em;
  padding: 1em;
  box-sizing: border-box;
  background: #f9f9f9;
  box-shadow: -3px 3px 10px #ccc;
}
.ShowIcon {
  position: absolute;
  top: .75em;
  left: .5em;
  width: .75em;
  height: .75em;
  opacity: .5;
  cursor: pointer;
  transition: .2s ease-in-out;

  &:hover {
    transform: scale(1.5);
  }
}
.CloseIcon {
  position: absolute;
  top: .75em;
  right: 1em;
  width: .75em;
  height: .75em;
  opacity: .5;
  cursor: pointer;
  transition: .2s ease-in-out;

  &:hover {
    transform: scale(1.5);
  }
}
.Collapse {
  position: relative;
  margin-bottom: 1em;
  box-shadow: 0 1px 0 0 #ccc;
}
.Collapsable {
  height: auto;
  max-height: 0;
  overflow: hidden;
  transition: all .2s ease-in-out;
}
.open {
  & + .Collapsable {
    max-height: 1000px;
  }
  .CollapseIcon {
    transform: rotate(90deg);
  }
}
.FormTitle {
  position: relative;
  cursor: pointer;
}
.CollapseIcon {
  position: absolute;
  top: .15em;
  right: .15em;
  width: .75em;
  height: .75em;
  opacity: .5;
  cursor: pointer;
  transition: .2s ease;
}
